import React, { Component } from 'react';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as commonActions from '../../state/commonActions';
import * as Util from '../../state/Util';
import Icons from '../Icons/Icons';
import LoadingColorSpinner from '../Icons/LoadingColorSpinner';
import InputCheckBox from '../CheckBox';

import './style.css';



class Profile extends Component {
  constructor(props) {
    super(props);  
    this.state = {
      active :false,
      loading:false,
      msg:false        
    };
  }
  componentDidMount() {
      window.localStorage.setItem('path',Util.Base64.encode('Profile'));
      window.localStorage.setItem('c_id',7);      
  }  
  componentWillUnmount(){
   

   }

   OnChangeGrabb(v){
      if(this.props._active){
         this.props.actions.updateGrabb({active:v});
      }
   }

   OnChangeActive(v){
      this.props.actions.updateActive_({active:v});     
   }

   reseatS(){
      this.setState({loading:true})
      commonActions.reseatServices().then((d)=>{
        this.setState({loading:false,msg:true,status:d})
        setTimeout(()=>{this.setState({loading:false,msg:false})},1500);   
      }); 
   }
  
  render() {   
     const {_info} = this.props;
      return (
         <div >
          
            <div className={'content_details'}/>         
            <div className={`hirecGFkZElucHV0`} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
               <div className="tab-label-title"><span>Profile</span></div>
               <div className="filter--cards--wrapers">
                 {/* <div className="filter--cards ">
                     <div className={`top--secction ${'settings'}`}>
                        <div className="service--areas">                        
                           <div className="area">
                              Reset Services
                           </div>                          
                        </div>    
                        <div className="actions">                                 
                           <div className="actions--icons"  onClick={this.reseatS.bind(this)}>
                              {loading?<LoadingColorSpinner stroke={'var(--color-base--hover)'} width={40} height={40}/>:
                              <Icons name={'arrow_right'} size={24}/>
                              }
                           </div>                                                                                   
                        </div> 
                     </div> 
                     <div className={`active--secction ${msg?'msg_open':'msg_close'}`}>
                           {status?<div className="success">Reset Success</div>:<div className="failed">Reset Failed</div>}    
                     </div>                                  
                  </div>  */}
                  <div className="filter--cards ">
                     <div className={`top--secction ${'_profile'}`}>
                        <div className="service--areas ">                        
                           <div className="area">
                              Email
                           </div>                          
                        </div>  
                        <div className="actions">                                 
                           {_info?_info.email:''}   
                                                                                                        
                        </div> 
                     </div>                                                       
                  </div>
                  <div className="filter--cards ">
                     <div className={`top--secction ${'_profile'}`}>
                        <div className="service--areas">                        
                           <div className="area">
                              Status
                           </div>                          
                        </div>  
                        <div className="actions"> 
                           {_info?_info.active?'Active':'Inactive':'Inactive'}
                                                                                                
                        </div> 
                     </div>                                                       
                  </div> 
                  <div className="filter--cards ">
                     <div className={`top--secction ${'_profile'}`}>
                        <div className="service--areas">                        
                           <div className="area">
                              Expire
                           </div>                          
                        </div>  
                        <div className="actions">      
                           {_info?date2pretyfy(_info.expire):null}                                                                                                      
                        </div> 
                     </div>                                                       
                  </div>              
               </div>
            </div>
         </div>
      ) 
  }
}






function mapStateToProps(state, ownProps) {
   return {      
      _info:state.common.activeUserInfo
   };
 }
 
 function mapDispatchToProps(dispatch) {
   return {
     actions: bindActionCreators(commonActions, dispatch)
   };
 }
 export default connect(mapStateToProps, mapDispatchToProps)(Profile);

 function date2pretyfy(dt) {
   var date = !isNaN(dt)?new Date(parseInt(dt.toString())):new Date();   
   return `${Util.monthsList_Short[date.getMonth()+1]} ${date.getDate()}, ${date.getFullYear()}`;  
 }