import Fight from './blocks';

import Logout from './Logout';


//import * as Subscription from '../state/subscription';

import {genId} from '../state/Util';

class Home extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
      route_list : [],
      setting:false,
      badUser:false
    };
  }



  componentWillMount(){  
    this.resize = this.resize.bind(this);
    window.addEventListener('resize',this.resize);
    //Util.addScroll2();
    var _Id = genId();
    //console.log(_Id);
    this.props.commonActions.setSubId(_Id);
    //Subscription._EventSourceFunction(this.props.commonActions,_Id);
   
    var _th = this;
    let accessToken = localStorage.getItem('_accessToken_');
    //let idToken =  localStorage.getItem('_idToken_');
  
    if(accessToken){      
      let Credential2G = {
        accessToken:accessToken,
        //idToken:idToken
      }
      var credential = commonActions._firebase.auth.GoogleAuthProvider.credential(Credential2G); 
      commonActions._firebase.auth().signInWithCredential(credential).then(function(result_signInWithCredential){
            
          let _user = result_signInWithCredential.additionalUserInfo.profile;
          _user['uid']=result_signInWithCredential.user.uid;
         
          _th.props.commonActions.UpdKeyValue({key:'user',value:_user});
          _th.props.commonActions.setAuthenticate(true);  
          _th.props.commonActions.appLoaded(true);
      }).catch(function(error) {   
       commonActions._firebase.auth().signInWithPopup(commonActions._providerAuth).then(function(result) {
          let _user = result.additionalUserInfo.profile;
          _user['uid']=result.user.uid;
          localStorage.setItem('_accessToken_', result.credential.accessToken);
          _th.props.commonActions.UpdKeyValue({key:'user',value:_user});
          _th.props.commonActions.setAuthenticate(true);  
          _th.props.commonActions.appLoaded(true);
          //localStorage.setItem('_idToken_', result.credential.idToken);      
        }).catch(function(error) { });
     });
    }else{      
      commonActions._firebase.auth().signInWithPopup(commonActions._providerAuth).then(function(result) {
        let _user = result.additionalUserInfo.profile;
        _user['uid']=result.user.uid;
     
        localStorage.setItem('_accessToken_', result.credential.accessToken);
        _th.props.commonActions.UpdKeyValue({key:'user',value:_user});
        _th.props.commonActions.setAuthenticate(true);  
        _th.props.commonActions.appLoaded(true);
        //localStorage.setItem('_idToken_', result.credential.idToken);      
      }).catch(function(error) { });
    }
  }

  componentWillUnmount(){
    window.removeEventListener('resize',this.resize);
  }

  componentDidMount(){
    this.props.commonActions.LoadData();
  }

  resize(e){
    if(this.props.commonActions){
      this.props.commonActions.ScreenSize(window.outerWidth);
    }   
  }

 /*
 
  <Route path="/blocks" component={Blocks} /> 
                <Route path="/logs" component={Logs} /> 
 */

  render() {
    const {appLoaded,authenticate,_active} = this.props;
    if(appLoaded){
      if(authenticate){
        return (
          <div className={`App ${_active?'':'not_active'}`}>
             <Header/>          
            <div id="content_body">    
              <Switch>
                <Route exact path="/"  component={Fight} />                
                <Route path="/fights" component={Fight} />   
                <Route path="/logout" component={Logout} />                                                     
              </Switch>
            </div>
            <DialogHRM/>
            <SlideOptionHRM/>
            <ViewHRM/>
          </div>
        );
      }else{
        return (<Login/>)
      }      
    }else{
      return (
        <div className="loadingScreen">
          <LoadingColorSpinner stroke={'#fff'}/>
        </div>
      )
    }
  }  
}


function mapStateToProps(state, ownProps) {
  return {       
    appLoaded: state.common.appLoaded,
    _active:state.common._active, 
    authenticate: state.common.authenticate,

  };
}

function mapDispatchToProps(dispatch) {
  return {     
    commonActions: bindActionCreators(commonActions, dispatch)
  };
}


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Home));


