 handleSentEmail(){
   var email =  this.props.forms['Login']?this.props.forms['Login']['email']:null;
   if(email){
      Util.fetchPostUrl(`${GRAPHQLURL}/generateToken?email=${email}`) 
      .then(res => {
         if(res.status=== 200){
            this.setState({code:true,phonecode:res.phone})
         }else{
            this.setState({code:false})
         }        
      }).catch(error => {      
         this.setState({code:false})
      });
     
   }   
 }

